.edit-daily-intake{
    margin: 30em auto;
    width: 30em;
    padding: 45px;
    display: flex;
    height: 40vh;
    flex-direction: column;
   box-sizing: border-box;
   border-radius: 15px;
   text-align: center;
   font-family: 'Archivo', sans-serif;
   -webkit-box-shadow: -11px 5px 42px 1px rgba(176,166,176,1);
-moz-box-shadow: -11px 5px 42px 1px rgba(176,166,176,1);
box-shadow: -11px 5px 42px 1px rgba(176,166,176,1);

background-color: rgb(57,156,98);
}

.edit-daily-intake .x{
    display: flex;
    justify-content: right;
}


@media only screen and (max-width: 500px){

    .edit-daily-intake{
        height: auto;
        width: 80%;
    }

}

@media only screen and (min-width: 750px){
    .edit-daily-intake{
       height: auto;
       width: 40%;
       padding:30px;
   }

}